html {
  height: 100%;
}

body {
  font-family: "Inter", sans-serif;
  color: #1A1A1A;
  display: flex;
  flex-direction: column;
  position: relative;
  height: 100%;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #E9F0FF;
}
body:before {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  content: "";
  background: rgba(26, 26, 26, 0.9);
  z-index: 2;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out;
}
body.ios-browser input,
body.ios-browser select,
body.ios-browser textarea {
  font-size: inherit !important;
}
body.lock {
  overflow: hidden;
}
body.open-modal:before {
  opacity: 1;
  visibility: visible;
}
body textarea {
  max-height: 96px;
}

img {
  max-width: 100%;
  height: auto;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

a {
  color: #1E56D9;
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}
@media (hover: hover) {
  a:hover {
    cursor: pointer;
    color: #0745DE;
  }
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  a:hover {
    cursor: pointer;
    color: #0745DE;
  }
}
a:active {
  color: #003AC9;
}
button {
  border: 0;
  box-shadow: none;
  background: transparent;
}
@media (hover: hover) {
  button:hover {
    cursor: pointer;
  }
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  button:hover {
    cursor: pointer;
  }
}
button:focus {
  outline: 2px solid rgba(30, 86, 217, 0.2);
}

input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

ul {
  padding-left: 0;
  list-style: none;
}

li::marker {
  color: #1E56D9;
}

.container {
  width: 100%;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
  max-width: 1220px;
}

@keyframes apperanceRight {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes apperanceBottom {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeInTop {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeInTopDeep {
  0% {
    opacity: 0;
    transform: translate3d(0, 100px, 0);
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
@keyframes fadeInRight {
  0% {
    opacity: 0;
    transform: translateX(20px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes scaleX {
  0% {
    opacity: 0;
    transform: scaleX(0);
  }
  100% {
    opacity: 1;
    transform: scaleX(1);
  }
}
@keyframes spin {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
.btn {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  position: relative;
  text-transform: capitalize;
  text-decoration: none;
  white-space: nowrap;
  cursor: pointer;
  border: none;
  box-shadow: none;
  border-radius: 8px;
  outline: 2px solid transparent;
  transition: color 0.15s ease-in-out, background-color 0.3s ease-in-out, outline-color 0.15s ease-in-out;
}
.btn:hover, .btn:active {
  text-decoration: none;
}

.btn--full {
  color: #fff;
  background: #1E56D9;
  border-radius: 8px;
}
@media (hover: hover) {
  .btn--full:hover {
    color: #fff;
    background: #0745DE;
  }
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .btn--full:hover {
    color: #fff;
    background: #0745DE;
  }
}
.btn--full:active {
  color: #fff;
  background: #003AC9;
}
.btn--full:disabled {
  pointer-events: none;
}
.btn--empty {
  color: #1A1A1A;
  background: transparent;
  border: 1px solid rgba(97, 120, 140, 0.6);
}
@media (hover: hover) {
  .btn--empty:hover {
    border-color: #1E56D9;
    outline: 2px solid #1E56D9;
  }
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .btn--empty:hover {
    border-color: #1E56D9;
    outline: 2px solid #1E56D9;
  }
}
.btn--empty:active, .btn--empty.active {
  border-color: #1E56D9;
  outline: 2px solid #1E56D9;
}
.btn--empty:disabled {
  color: rgba(26, 26, 26, 0.7);
  background: transparent;
  border-color: rgba(97, 120, 140, 0.2);
  pointer-events: none;
}
.btn--empty-brand {
  color: #1E56D9;
  background: transparent;
  border: 1px solid #1E56D9;
}
@media (hover: hover) {
  .btn--empty-brand:hover {
    border-color: #1E56D9;
    outline: 2px solid #1E56D9;
  }
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .btn--empty-brand:hover {
    border-color: #1E56D9;
    outline: 2px solid #1E56D9;
  }
}
.btn--empty-brand:active, .btn--empty-brand.active {
  border-color: #1E56D9;
  outline: 2px solid #1E56D9;
}
.btn--empty-brand:disabled {
  color: rgba(26, 26, 26, 0.7);
  background: transparent;
  border-color: rgba(97, 120, 140, 0.2);
  pointer-events: none;
}
.btn--center {
  justify-content: center;
}

.btn--wide {
  width: 100%;
}
.btn--md {
  font-size: 16px;
  font-weight: 500;
  letter-spacing: -0.01em;
  height: 54px;
  padding: 0 16px;
}
.btn--sm {
  font-size: 16px;
  line-height: 0;
  font-weight: 500;
  letter-spacing: -0.01em;
  height: 38px;
  padding: 0 16px;
}

.btn__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
}
.btn__icon--sm {
  width: 24px;
  height: 24px;
}
.btn__icon--left {
  margin-right: 16px;
}
.btn__icon--right {
  margin-left: auto;
}
.btn__icon--circle {
  border-radius: 50%;
  overflow: hidden;
}
.btn__icon img {
  max-width: 100%;
  max-height: 100%;
}
.btn__check {
  position: absolute;
  top: 15px;
  right: 16px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out;
}
.btn.active .btn__check {
  opacity: 1;
  visibility: visible;
}

.converter {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  position: relative;
  background: #fff;
  height: 100%;
  max-width: 360px;
}
.converter-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
@media (min-width: 1200px) {
  .converter-wrap {
    padding: 40px 0;
  }
}
.converter__header {
  padding: 24px 20px 0;
}
.converter__body {
  position: relative;
  flex: 1;
  overflow: auto;
  overflow-x: hidden;
  padding: 0 20px;
  margin: 20px 0;
}
.converter__body.scrollable {
  padding-right: 1px;
}
.converter__footer {
  padding: 20px 20px 15px;
}
.converter__logo {
  max-width: 226px;
  margin: 0 auto;
}
.converter__address {
  font-size: 14px;
  color: rgba(26, 26, 26, 0.7);
  margin-top: 15px;
  text-align: center;
}
.converter__list {
  margin-top: 8px;
}
.converter__list-title {
  margin-bottom: 18px;
}
.converter__list-item + .converter__list-item {
  margin-top: 8px;
}
.converter__btns--row {
  display: grid;
  grid-template-columns: 54px 1fr;
  grid-gap: 16px;
}
.converter__btns--col .btn + .btn {
  margin-top: 16px;
}
.converter__powered {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-top: 15px;
}
.converter__powered span {
  font-size: 12px;
  color: rgba(26, 26, 26, 0.7);
}
.converter__powered svg {
  margin-left: 5px;
  margin-top: 3px;
}

.price {
  border: 1px solid rgba(97, 120, 140, 0.4);
  border-radius: 8px;
  padding: 16px;
  text-align: center;
  margin-top: 16px;
}
.price__text {
  font-style: 16px;
  margin-bottom: 4px;
}
.price__num {
  font-family: "Manrope", sans-serif;
  font-size: 48px;
  font-weight: 600;
  letter-spacing: -0.02em;
}

.search {
  position: relative;
  width: 100%;
}
.search__title {
  font-size: 16px;
  margin-bottom: 16px;
}
.search__field {
  padding-left: 23px;
  border: solid 1px rgba(97, 120, 140, 0.6);
  border-radius: 8px;
  height: 54px;
  width: 100%;
}
.search__field::placeholder {
  color: rgba(26, 26, 26, 0.7);
}
.search__submit {
  position: absolute;
  right: 2px;
  bottom: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0;
  border-radius: 0 8px 8px 0;
  cursor: pointer;
  height: 50px;
  width: 50px;
  background: #fff;
}

.loader {
  text-align: center;
  margin-top: 20px;
}
@media (min-width: 1200px) {
  .loader {
    margin-top: 80px;
  }
}
.loader__ring {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 72px;
  height: 72px;
  margin: 0 auto;
  animation: spin 1.4s ease infinite;
}
.loader__text {
  margin-top: 24px;
}
.loader__response {
  font-family: "Manrope", sans-serif;
  font-size: 32px;
  font-weight: 600;
  letter-spacing: -0.02em;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out;
  margin-top: 6px;
}
.loader__success {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out;
}
.loader.success .loader__text, .loader.success .loader__ring {
  display: none;
}
.loader.success .loader__response, .loader.success .loader__success {
  opacity: 1;
  visibility: visible;
}

.qr {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-top: 8px;
}
.qr__code {
  width: 216px;
  height: 216px;
  padding: 16px;
  margin: 24px 0;
  border: 1px solid rgba(97, 120, 140, 0.4);
  border-radius: 8px;
}

.footer {
  padding: 40px 0;
  position: relative;
}
.footer__row {
  display: flex;
  flex-direction: column;
}
@media (min-width: 992px) {
  .footer__row {
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer:after {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  content: "";
  height: 1px;
  background: rgba(97, 120, 140, 0.2);
}
.footer__copy, .footer__policy, .footer__tel {
  font-size: 12px;
  line-height: 18px;
}
.footer__copy a, .footer__policy a, .footer__tel a {
  color: #1A1A1A;
}
@media (hover: hover) {
  .footer__copy a:hover, .footer__policy a:hover, .footer__tel a:hover {
    color: #1E56D9;
  }
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .footer__copy a:hover, .footer__policy a:hover, .footer__tel a:hover {
    color: #1E56D9;
  }
}